import React, { useState, useEffect } from "react";
import CartWidget from "./cartWidget/CartWidget";
import "./navBar.css";
import { Link } from "react-router-dom";
import { useGlobalContext } from "../../globalContext";
import SignButton from "./SignButton/SignButton";
import SubMenu from "./SubMenu/SubMenu";
import { RiMenuFill } from "react-icons/ri";

function NavBar() {
  const [navbarBackground, setNavbarBackground] = useState(false);
  const [showNavbar, setShowNavbar] = useState(false);
  const [showScroll, setShowScroll] = useState(true);
  const [userPhoto, setUserPhoto] = useState();
  const {
    itemsOnCart,
    currentUser,
    pageError,
    setErrorRequest,
  } = useGlobalContext();
  const links = [
    { name: "home", id: 1 },
    { name: "productos", id: 2 },
    { name: "nosotros", id: 3 },
    { name: "contacto", id: 4 },
  ];
  const toggleNavbar = () => {
    setShowNavbar(!showNavbar);
    window.document.body.style.overflow = "hidden";
  };

  useEffect(() => {
    if (currentUser !== null) {
      setUserPhoto(currentUser.photoURL);
    }
  }, [currentUser]);

  const toggleScroll = () => {
    window.document.body.style.overflow = "scroll";
    setShowNavbar(false);
  };

  useEffect(() => {
    !showNavbar && (window.document.body.style.overflow = "scroll");
  }, [showNavbar]);

  useEffect(() => {
    const changeBackground = () => {
      if (window.scrollY > 80) {
        setNavbarBackground(true);
      } else setNavbarBackground(false);
    };
    window.addEventListener("scroll", changeBackground);
    return () => window.removeEventListener("scroll", changeBackground);
  }, []);

  return (
    <>
      {!pageError && (
        <div
          className={navbarBackground ? "navbar active " : "navbar"}
          style={{ height: `${showNavbar ? "100vh" : "80px"}` }}
        >
          <div className="brand-name">
            <Link
              to="/"
              style={{ border: "none" }}
              onClick={() => toggleScroll()}
            >
              <h3 className="simpleShop">SimpleCloths</h3>
            </Link>
          </div>
          <div
            className="toggle"
            style={{ height: `${showNavbar ? "100vh" : "0"}` }}
          >
            <ul>
              {links.map((link) => {
                const { name, id } = link;
                return name === "productos" ? (
                  <div
                    key={id}
                    to={`/${name}`}
                    className={"category"}
                    onClick={() => {
                      setErrorRequest({ itemList: false });
                      toggleScroll();
                    }}
                  >
                    <SubMenu
                      category={"productos"}
                      className={"category"}
                      toggleScroll={toggleScroll}
                    />
                  </div>
                ) : (
                  <Link key={id} to={`/${name}`} onClick={() => toggleScroll()}>
                    <li>{name}</li>
                  </Link>
                );
              })}
            </ul>
            <div className="user-section">
              {itemsOnCart === null ||
              undefined ||
              itemsOnCart.length === 0 ? null : (
                <Link to="/cart" id="cartWidget" onClick={() => toggleScroll()}>
                  <CartWidget />
                </Link>
              )}
              {currentUser !== null && (
                <Link
                  to="/userProfile"
                  id="profile-link"
                  onClick={() => toggleScroll()}
                >
                  <img
                    src={userPhoto}
                    className="user-profile-link"
                    alt={userPhoto}
                  />
                </Link>
              )}
              <SignButton />
            </div>
          </div>
          <button className="hamburger-toggle" onClick={toggleNavbar}>
            <RiMenuFill />
          </button>
        </div>
      )}
    </>
  );
}

export default NavBar;
